table{
    border-collapse: collapse;
    border: 2px solid;
    margin-top: 30px;
    width: 100%;
    margin-bottom: 30px;
}
td,
th{
    padding: 5px 15px;
    border: 1px solid #682b2b;
}

.checked ~ *{
    background: #DE3C4B;
}
.checked ~ .done{
    background: #66c958;
}
.checking ~ *{
    background: #ff909a;
}
.checking ~ .done{
    background: #66c958;
}

.in.progress ~ *{
    background: #F2ED6F;
}

.in.progress ~ .done{
    background: #66c958;
}

.todo ~ *{
    background: #e0d7d7;
}

.todo ~ .done{
    background: #66c958;
}

th{
    font-size: 16px;
    text-transform: uppercase;
    font-weight: normal;
}

table a{
    font-size: 16px;
    text-transform: uppercase;
    color: #333333;
    text-decoration: none;
    transition: all ease .2s;
    font-weight: normal;
}

table a:hover{
    color: #3443ff;
}

.legend-item{
    display: block;
    margin-bottom: 10px;
}

.legend-item span{
    width: 100px;
    height: 25px;
    display: inline-block;
    margin-right: 20px;
}

.red{
    background: #DE3C4B;
}

.green{
    background: #66c958;
}

.grey{
    background: #e0d7d7;
}

.yellow{
    background: #F2ED6F;
}

.rose{
    background: #ff909a;
}